import React from "react";
import { Link, useParams, useRouteMatch } from "react-router-dom";

function KnifePage() {
  const prevPage = useRouteMatch().path.match(/[^:]+/)[0];
  const { id } = useParams();

  return (
    <>
      <div>Knife page: {id}</div>
      <div>
        <Link to={prevPage}>go back</Link>
      </div>
    </>
  );
}

export default KnifePage;
